@import "https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap",
  "https://fonts.googleapis.com/css2?family=Special+Elite&display=swap";
@import "./scss/reset", "./scss/variables", "./scss/mixins", "./scss/animations";

.page {
  min-width: 360px;

  font-family: "Manrope", Helvetica, Arial, sans-serif;
  @include font-config(
    $size: 1.6rem,
    $colour: $clr-f-black,
    $weight: normal,
    $lh: 1.5
  );
}

.section-padding {
  font-size: 1rem;

  @include mq("desktop") {
    font-size: 0.8rem;
  }

  @include mq("tablet") {
    font-size: 0.745rem;
  }
}

::selection {
  background-color: $clr-primary-orange;
  color: $clr-f-white;
}
